$grass: rgba(123, 158, 54, 1);
$snow-blue: rgba(2, 83, 126, 1);

// TOP-NAV: COMPANY NAME, LOGO, AND CALL TO ACTION
.main-nav {
  display: flex;
  background-color: rgb(230, 230, 230);
  height: 75px;
  margin: 0;
  width: 100%;
  position: fixed;
  z-index: 100;

  .logo-name {
    display: flex;
    justify-content: center;
    width: 10%;
    height: 100%;

    svg {
      align-self: center;
      width: auto;
      margin: auto;
    }

    .company-name {
      align-self: center;
      color: #25352C;
      margin-left: 15px;
      text-align: center;
      font-style: normal;
      font-variant-ligatures: normal;
      font-variant-caps: normal;
      font-variant-numeric: normal;
      font-variant-east-asian: normal;
      font-weight: 400;
      font-stretch: normal;
      font-size: 1.2em;
      line-height: 1.4em;
      font-family: avenir, sans-serif;

      .snow-link {
        color: #000000;
        text-decoration: none;
      }

      span:first-of-type {
        font-weight: 400;
      }

      .landscape-link {
        color: #000000;
      }
    }
  }

  .main-nav-links {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    width: auto;
    z-index: 100;

    .nav-link {
      align-self: center;
      margin: 0 20px;
      font-family: avenir, sans-serif;
      text-decoration: none;
      color: #25352C;
      transition: 0.3s;
    }

    .nav-link:first-of-type {
      margin: 0 20px 0 0;
    }

    .nav-link:hover {
      color: $snow-blue;
    }
  }
}

// IFRAME TOOLTIP IN TOP-NAV LINK TO SNOW REMOVAL
.thumbnail img {
  position: absolute;
  width: 200px;
  height: auto;
  margin-top: 45px;
  margin-left: 140px;
  z-index: 30;
}

// HERO IMAGE AND TITLE SECTION

.hero-img {
  height: auto;
  width: 100vw;
  margin-top: 71px;
}

.hero-block {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: $snow-blue;
  height: auto;
  width: 100vw;
  margin-top: -5px;

  .hero-cta {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: inherit;

    h4 {
      margin-top: 0;
      color: #FFFFFF;
    }

    .hero-title {
      color: rgb(255, 255, 255);
      font-size: 3rem;
      font-family: avenir, sans-serif;
      text-align: center;
      text-shadow: rgba(0, 0, 0, 0.4) 0px 4px 5px;
      margin-bottom: 15px;
    }

    .snow-link-btn > * {
      cursor: pointer;
      align-self: center;
      width: 160px;
      height: auto;
      padding: 10px 20px;
      background-color: #003C7B;
      color: #FFFFFF;
      border: 1px solid transparent;
      border-radius: 33px;
      margin: auto;
      box-shadow: 0px 2px 5px 1px rgba(0,0,0,0.55);
    }

    .quote-cta {
      text-decoration: none;
      color: inherit;
    }

    .hero-social-cta {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      width: 10%;
      padding: 25px 0;
    }
  }
}

// MOBILE HERO SECTION
@media (max-width: 470px) {
  .hero-block {
    position: relative;
    background-color: $snow-blue;
    height: auto;
    width: 100%;
    margin-top: -5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .hero-social-container {
      display: none;
    }

    .hero-cta {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 70%;

      .hero-title {
        color: rgb(255, 255, 255);
        font-size: 2rem;
        font-family: avenir, sans-serif;
        text-align: center;
        text-shadow: rgba(0, 0, 0, 0.4) 0px 4px 5px;
      }

      .snow-link-btn {
        margin-bottom: 30px;
      }
    }
  }
}

//INFO BOXES

.info-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  height: auto;
  background-color: rgba(230, 230, 230, .5);
  padding: 80px 0;

  .info-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: auto;
    width: 23vw;
    margin: 20px 3vw 0;

    img {
      height: auto;
      width: 80%;
      margin-bottom: 5px;
      border: 1px solid transparent;
      border-radius: 100%;
    }

    h4 {
      font-size: 20px;
      margin-bottom: 5px;
    }

    p {
      font-size: 16px;
    }
  }
}

