// QUOTES page
.quote-page-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding-top: 85px;

  .quote-hero {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    height: calc(100vh - 65px);

    h1 {
      color: $snow-blue;
      font-size: 3rem;
      font-family: avenir, sans-serif;
      text-align: center;
      text-shadow: rgba(0, 0, 0, 0.4) 0px 4px 5px;
      margin-bottom: 0;
    }

    p {
      margin-top: 0;
      font-family: omnes-cond, sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      color: rgba(50, 65, 50, 1);
      line-height: 1.5;
      padding: 0px 8vw;
    }

    p:nth-of-type(2) {
      margin-top: auto;
      margin-bottom: 15px;
    }

    .quote-down-arrow {
      color: $snow-blue;
      position: relative;
      cursor: pointer;
      font-size: 33px;
      border: 2px solid $snow-blue;
      border-radius: 100%;
      padding: 6px 22px 11px;
      transition: 0.35s;
      margin-bottom: auto;
    }

    .quote-down-arrow:hover {
      background-color: $snow-blue;
      color: white;
    }
  }

.quote-tools-ref {
  width: 100%;
  height: 100vh;
  padding-top: 85px;

  .quote-tools-container {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 96%;
    height: calc(100% - 95px);
    margin: auto 2%;
    background-color: #F9F9F9;
    border: 1px solid transparent;
    border-radius: 5px;
    box-shadow: 1px 2px 5px 1px rgba(0,0,0,0.35);

    .input-tools-container {
      width: 100%;
      height: 100%;

      .input-directions {
        color: rgba(50, 65, 50, 1);
        text-align: center;
      }

      .input-wrapper {
        width: 65%;
        max-width: 500px;
        height: auto;
        margin: calc(40vh - 85px) auto 0;

        .google-maps-input {
          width: 100%;
          background: #fff;
          border-radius: 8px;
          box-sizing: border-box;
          border: 1px solid transparent;
          padding: 12px 75px 11px 10px;
          box-shadow: 1px 2px 5px 1px rgba(0,0,0,0.35);
        }
      }
    }

    button {
      cursor: pointer;
      outline: none;
      border: 1px solid transparent;
      padding: 10px 15px;
      font-family: Lato, arial, sans-serif;
      font-size: 14px;
    }

    .button-group {

      button:first-of-type {
        border-radius: 4px 0 0 4px;
      }

      button:nth-of-type(2) {
        border-radius: 0 4px 4px 0;
      }

      button.selected {
        background-color: $snow-blue;
        color: #FFFFFF;
        box-shadow: 0px 2px 5px 1px rgba(0,0,0,0.55);
      }
    }

    .toggle-price-details {
      margin: auto;
    }

    .quote-details {
      text-align: center;
      display: flex;
      flex-direction: column;
      margin: auto;

      button {
        margin-top: 10px;
      }

      button.selected {
        background-color: $snow-blue;
        color: #FFFFFF;
        box-shadow: 0px 2px 5px 1px rgba(0,0,0,0.55);
      }

      button:hover {
        background-color: $snow-blue;
        color: #FFFFFF;
        transition: .4s;
      }
    }

    .deposit-container {
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 20px;

      button {
        display: flex;
        align-content: center;
        justify-content: center;
        width: auto;
        max-width: 300px;
        padding: 13px 35px;
        margin: 60% 0;
        border: 2px solid transparent;
        border-radius: 3px;
        color: #FFFFFF;
        background-color: $snow-blue;
        font-family: avenir, sans-serif;
        font-size: 16px;
      }
    }

    .confirmation-buttons {
      margin: auto;

      button {
        margin: 0 12px;
        border-radius: 4px;
      }

      button > * {
        color: black;
        text-decoration: none;
      }
    }

  }

    .map-tools-container {
      display: flex;
      flex-direction: row-reverse;
      width: 100%;
      height: 100%;

      .google-map-placeholder {
        background-color: transparent;
      }

      .google-map, .google-map-placeholder {
        height: 100%;
        width: 100%;
      }

      .draw-area-prompt {
        position: absolute;
        text-align: center;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        width: calc(75.7%);
        z-index: 10;
        background-color: #FFFFFF;

        p {
          width: 100%;
        }

        button {
          margin: 0px 10px 15px;
        }
      }

      .google-map.job-type-expanded {
        height: 0;
        width: 0;
      }

      .change-address {
        position: absolute;
        cursor: pointer;
        text-decoration: underline;
        color: $snow-blue;
        top: 5px;
        left: 10px;
        right: auto;
      }

        .job-type-container {
          display: flex;
          flex-direction: column;
          text-align: center;
          justify-content: center;
          align-items: center;
          flex-wrap: wrap;
          width: 30vw;
          height: inherit;
          background-color: inherit;
          transition: .8s;

          .presentation-mode {
            margin-top: 30px;
            width: 95%;
            height: auto;
            background-color: #F9F9F9;

            .slide.primary {
              display: flex;
              justify-content: center;
              height: 100%;
              background-color: #F9F9F9;
            }
          }

          p {
            margin-top: 0px;
            font-size: 16px;
          }

          .house-confirmation {
            margin-top: 0%;

            span:nth-of-type(2) {
              font-size: 13px;
              font-style: italic;
            }
          }

          .job-type-tile {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 33px;
            width: 90%;
            max-width: 600px;
            padding: 0 5px;
            margin: 3%;
            background-color: #FFF;
            border: 1px solid transparent;
            border-radius: 5px;
            box-shadow: 1px 2px 5px 1px rgba(0,0,0,0.25);
            transition: 0.35s;

            .clickable-span {
              width: 100%;
            }
          }

          .tile-selected, .job-type-tile:hover {
            background-color: rgba(2, 83, 126, 1);
            color: #FFFFFF;
          }

          .disabled {
            cursor: not-allowed;
          }

          .areas-in-progress {
            background-color: rgba(2, 83, 126, .6);
            color: #FFFFFF;
          }
        }
        .job-type-container.expanded {
          display: flex;
          justify-content: center;
          flex-direction: row;
          width: 100%;
          height: 100%;
          text-align: center;

          p {
            height: 5%;
          }

          .small-help-text {
            font-size: 13px;
            font-style: italic;
          }

          .job-type-tile.continue-button {
            color: #FFFFFF;
            margin: 20px auto;
            background-color: $snow-blue;
          }

          .job-selections {
            width: 100%;
            height: 60%;
            display: flex;
            justify-content: center;
            flex-direction: row;
            flex-wrap: wrap;

            .job-type-tile {
              width: 15%;
              height: auto;
            }

          .job-type-tile.back-button {
            margin-bottom: 5%;
            background-color: #e6e6e6;
          }


          .job-type-tile.details-shown {
            display: flex;
            justify-content: center;
            align-content: center;
            flex-direction: column;
            padding: 5px;
            height: auto;
            flex-wrap: wrap;
            background-color: rgba(2, 83, 126, .9);
            color: #FFFFFF;
          }

          .job-type-tile.details {
            display: flex;
            flex-direction: column;
            height: auto;
            width: 95%;
            flex-wrap: wrap;
            border-radius: 5px;
            color: #000000;
            background-color: #FFFFFF;

            .detail {
              display: flex;
              flex-direction: row;
              height: auto;
              width: 100%;
              align-items: center;
              justify-content: center;

              button {
                height: 30px;
                padding: 5px 10px;
                margin: 0 8px;
                border: 1px solid transparent;
                border-radius: 3px;
              }
            }
          }
        }
      }
      }
      @media (max-width: 450px) {
        .quote-tools-container {
          width: 100%;
          margin: 0;
        }

        .map-tools-container {
          width: 100%;
          flex-direction: column-reverse;

          .job-type-container {
            flex-direction: row;
            width: 100%;

            .presentation-mode {
              margin-top: 30px;
              height: auto;
              background-color: #F9F9F9;

              .slide.primary {
                display: flex;
                justify-content: center;
                height: 100%;
                background-color: #F9F9F9;
              }
            }

            .house-confirmation {
              margin-bottom: 0;
              justify-content: center;
              align-items: center;
              flex-direction: row;
            }
          }

          .job-type-container.expanded {
            margin-top: 20px;
            text-align: center;

            p:nth-of-type(1) {
              margin-bottom: 0px;
            }

            .small-help-text {
              margin-top: 0px;
            }

            .job-selections {
              margin-top: 0;

              .job-type-tile {
                height: auto;
                width: auto;
              }
            }
          }
        }
      }
    }

  .quote-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 15px auto;
    width: 100%;
    max-width: 700px;
    height: calc(100vh - 90px);
    background-color: #F9F9F9;
    overflow-y: auto;

    label:first-of-type {
      margin-top: auto;
    }

    label {
      font-family: omnes-cond, sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      margin-left: 6%;
      margin-top: 5px;
      margin-bottom: 3px;
    }

    .quote-input {
      width: 90%;
      background: #fff;
      border-radius: 8px;
      box-sizing: border-box;
      border: 1px solid transparent;
      padding: 12px 75px 11px 10px;
      margin: 3px auto;
      box-shadow: 0px 1px 3px 1px rgba(0,0,0,0.25);
    }

    .get-quote-button {
      cursor: pointer;
      align-self: center;
      width: 160px;
      height: auto;
      padding: 10px 20px;
      background-color: #003C7B;
      color: #FFFFFF;
      border: 1px solid transparent;
      border-radius: 33px;
      margin: auto;
      box-shadow: 0px 2px 2px 0px rgba(0,0,0,0.85);
    }
  }
}

// ABOUT SECTION
