        font-family: avenir, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: calc(22px + 2%);
        color: $snow-blue;
        text-align: center;
        margin-top: 0;
      }

      p {
        font-family: omnes-thin, sans-serif;
        font-style: normal;
        font-weight: 100;
        font-size: 16px;
        line-height: 1.5;
        text-align: left;
      }
    }
  }
}

// TESTIMONIALS PAGE
.testimonial-page-container {
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(230, 230, 230, .5);
  text-align: center;

  .testimonials-container {
    display: flex;
    height: calc(100vh - 75px);
    width: inherit;
    margin-top: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
      margin: auto;
    }

    .presentation-mode {
      width: 95%;
      height: auto;

      .slide {
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-color: $snow-blue;
        height: auto;
        padding: 0 5%;
        color: #FFFFFF;

        p {
          font-size: calc(18px + 2%);
        }
      }
    }
    a {
      background-color: #d32323;
      color: #FFFFFF;
      padding: 15px 25px;
      border: 1px solid #d32323;
      border-radius: 3px;
      margin: auto;
      font-size: 18px;
      text-decoration: none;
    }
  }
}

// GALLERY PAGE
.gallery-page-container {
  width: 100%;
  height: auto;
  background-color: rgba(230, 230, 230, .5);
  padding-top: 80px;
  text-align: center;

  .gallery-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 25px 50px;
  }
}

.footer-container {
  position: relative;
  z-index: 100;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 390px;
  bottom: 0;

  .footer-icon-container {
    display: flex;
    flex-direction: row;
    width: inherit;
    height: inherit;

    div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      height: 100%;
      width: 33.333333%;
      width: calc(100% /3);
      color: #FFFFFF;
      text-align: center;
    }
    .footer-1 {
      background-color: $snow-blue;
    }
    .footer-2 {
      background-color: rgba(52, 133, 176, 1);
    }
    .footer-3 {
      background-color: rgba(102, 183, 226, 1);
    }

    svg {
      width: 30%;
      height: 30%;
      margin-bottom: 6%;
    }
    span {
      font-size: 20px;
    }

    @media(max-width: 450px) {
      span {
        font-size: 13px;
      }
    }
  }

  .footer-contact {
    display: flex;
    flex-direction: row;
    width: inherit;
    height: 70px;
    padding: 30px 0;
    background-color: rgba(35, 50, 43, 1);

    div {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #FFFFFF;
      border-right: 1px solid #FFFFFF;
      width: 25%;
      height: inherit;
      padding: 0 5px;

      span {
        margin-top: 5px;
        font-size: calc(14px + 1%);
      }
    }
    div:nth-child(3n) {
      display: flex;
      flex-direction: column;
    }
    div:nth-child(1n) {
      a:nth-child(1n) {
        margin-right: 2.5%;
      }
      a:nth-child(2n) {
        margin-left: 2.5%;
      }
    }
  }
}

// QUOTE CONFIRMATION PAGE
.confirmation-page-wrapper {
  position: relative;
  z-index: 110;
  background: url('Util/images/cut-grass.jpg');
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;

  .confirmation-page-greeting {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
    z-index: 120;
    background: inherit;
    width: 90%;
    padding: 0 5%;
    text-shadow: 1px 1px 1px #000000;

    h1 {
      margin-top: 15vh;
      font-family: avenir, sans-serif;
      font-size: calc(26px + 2vw);
    }

    p {
      position: absolute;
      margin-top: 40vh;
      font-family: avenir, sans-serif;
      font-size: calc(18px + 1vw);
      font-weight: 600;
    }

    span {
      position: absolute;
      margin-top: 48vh;
      font-family: avenir, sans-serif;
      font-size: calc(22px + 2vw);
      font-weight: 600;
    }

    .down-arrow {
      margin-top: 72vh;
      font-size: 33px;
      border: 2px solid white;
      border-radius: 100%;
      padding: 4px 19px 12px;
      transition: 0.35s;
    }

    .down-arrow:hover {
      background-color: rgba(255, 255, 255, .3)
    }
  }
}

.confirmation-quote-breakdown {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100vh;
  width: 90%;
  padding: 0 5%;
  color: #FFFFFF;
  background-color: rgba(35, 50, 43, 1);
  z-index: 110;

  h2 {
    margin-top: 8vh;
    font-family: avenir, sans-serif;
    font-size: calc(26px + 2vw);
  }

  p {
    font-family: avenir, sans-serif;
    font-size: 20px;
    line-height: 1.5;
    margin: 0 auto auto auto;
  }

  p:nth-of-type(2) {
    font-size: 16px;
    margin-top: auto;
  }

  span {
    font-family: avenir, sans-serif;
    font-size: 20px;
    font-weight: 500;
  }

  .confirmation-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: auto;

    button:first-of-type {
      margin-right: 15px;
    }

    button:first-of-type > * {
      text-decoration: none;
      color: #000000;
    }

    .confirm-quote-button {
      margin-left: 15px;
      color: #FFFFFF;
      background-color: $snow-blue;
      padding: 15px 25px;
      border: 2px solid transparent;
      border-radius: 3px;
      font-family: avenir, sans-serif;
      font-size: 16px;
    }

    button {
      padding: 15px 25px;
      border: 2px solid transparent;
      border-radius: 3px;
      font-family: avenir, sans-serif;
      font-size: 16px;
    }
  }
}

// USED TO PRELOAD IMAGES WHILE LOADING SCREEN DISPLAYS
.hidden {
  display: none;
}

// LOADING PAGE ANIMATION AND LAYOUT
.loading-page-wrapper {
  position: absolute;
  z-index: 150;
  background-color: #2DB82D;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  svg {
    position: relative;
    z-index: 115;
    height: 25vh;
    width: 25vh;
    animation: svg 2s linear infinite;
  }

  @keyframes svg {
    from {
      height: 25vh;
      width: 25vh;
      animation-timing-function: ease-out;
    }
    50% {
      height: 30vh;
      width: 30vh;
    }
    to {
      height: 25vh;
      width: 25vh;
    }
  }
}

